.ib-block-control__image {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.ib-block-control__image .components-base-control__field {
  position: relative;
}
.ib-block-control__image .components-responsive-wrapper__content {
  max-width: fit-content;
}
.ib-block-control__image .ib-block-control__image-actions {
  position: absolute;
  top: 0;
  right: 0;
  background-color: #fff;
}
.ib-block-control__image .ib-block-control__image-size {
  margin-top: 10px;
}
.ib-block-control__image .ib-block-control__image-size select {
  text-transform: capitalize;
}